import { Component, OnInit } from '@angular/core';
import {DarkSideService} from '../dark-side.service';
import {AuthService} from '../auth.service';
import {animate, style, transition, trigger} from '@angular/animations';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css'],
  animations: [
    trigger(
      'inOutAnimation',
      [
        transition(
          ':enter',
          [
            style({ opacity: 0 }),
            animate('0.2s ease-out',
              style({ opacity: 1 }))
          ]
        ),
        transition(
          ':leave',
          [
            style({ opacity: 1 }),
            animate('0.2s ease-in',
              style({ opacity: 0 }))
          ]
        )
      ]
    )
  ]
})
export class FooterComponent implements OnInit {

  isModalOpen = false;

  password: string;

  constructor(public darkSideService: DarkSideService, public authService: AuthService) { }

  ngOnInit() {
    if (localStorage.getItem('darkside') === '1') {
      this.darkSideService.darkSideOn = true;
    } else if (localStorage.getItem('darkside') === '0') {
      this.darkSideService.darkSideOn = false;
    }
  }

  changeSide() {
    this.darkSideService.darkSideOn = !this.darkSideService.darkSideOn;
    if (this.darkSideService.darkSideOn) {
      localStorage.setItem('darkside', '1');
    } else {
      localStorage.setItem('darkside', '0');
    }
  }

  authModalOpen() {
    this.isModalOpen = !this.isModalOpen;
  }

  closeModal(event) {
    if (event.target.className.includes('footer__auth-block')) {
      this.isModalOpen = !this.isModalOpen;
    }
  }

  authCheck() {
    if (this.authService.auth(this.password)) {
      this.isModalOpen = !this.isModalOpen;
    }
  }

}
